.lastsection {
  background-image: url('../img/lastback.png');
  background-repeat: no-repeat;
  background-size: cover;
  height: 669px;
  @media only screen and (max-width: 768px) {
    height: 675px; } }
.contactforsec__wrap {
  position: relative;
  h3 {
    color: #fff;
    font-size: 24px;
    font-weight: bold;
    font-family: "OpenSans";
    text-transform: uppercase;
    padding-top: 85px;
    padding-left: 300px;
    padding-bottom: 180px;
    @media only screen and (max-width: 768px) {
      padding-bottom: 100px; }
    @media only screen and (max-width: 375px) {
      text-align: center;
      padding-left: 0; } } }
.clientform {
  padding-left: 245px;
  @media only screen and (max-width: 768px) {
    padding-left: 155px; }
  @media only screen and (max-width: 375px) {
    padding-left: 0; } }
.clientinfo {
  display: flex;
  justify-content: center;
  position: relative;
  .client {
    border: 0;
    background: 0;
    border-bottom: 1px solid white;
    color: #fff;
    width: 100%;
    margin-bottom: 30px;
    @media only screen and (max-width: 768px) {
      width: 50%;
      margin-left: 25%; }
    @media only screen and (max-width: 375px) {
      width: 75%;
      margin-left: 10%; } }
  .clienttel {
    border: 0;
    background: 0;
    border-bottom: 1px solid white;
    color: #fff;
    margin-bottom: 30px;
    margin-right: 50px;
    width: 46%;
    @media only screen and (max-width: 768px) {
      width: 35%;
      margin-left: 25%; }
    @media only screen and (max-width: 375px) {
      width: 75%;
      margin-left: 10%; } }
  .town {
    border: 0;
    background: 0;
    border-bottom: 1px solid white;
    color: #fff;
    margin-bottom: 30px;
    width: 47%;
    @media only screen and (max-width: 768px) {
      width: 35%;
      margin-left: 25%; }
    @media only screen and (max-width: 375px) {
      width: 75%;
      margin-left: 10%; } }
  .clientbtn {
    display: block;
    width: 440px;
    height: 55px;
    background-color: #1cc4ca;
    border: none;
    border-radius: 25px;
    font-size: 24px;
    font-weight: bold;
    font-family: "OpenSans";
    color: #fff;
    margin-left: 350px;
    @media only screen and (max-width: 1024px) {
      margin-left: 270px; }
    @media only screen and (max-width: 768px) {
      margin-top: 55px;
      margin-left: 100px; }
    @media only screen and (max-width: 375px) {
      width: 100%;
      margin-left: 0;
      margin-bottom: 25px; } }
  .pricebtn {
    position: absolute;
    bottom: 0;
    left: 310px;
    p {
      font-size: 48px;
      font-weight: bold;
      font-family: "OpenSans";
      color: #1cc4ca;
      display: inline-block; }
    span {
      font-size: 24px;
      font-weight: bold;
      font-family: "OpenSans";
      text-decoration: line-through;
      display: inline-block;
      color: #b9c7cc; }
    @media only screen and (max-width: 1024px) {
      left: 225px; }
    @media only screen and (max-width: 768px) {
      bottom: 80px;
      left: 318px; }
    @media only screen and (max-width: 375px) {
      left: 30px;
      padding-bottom: 15px; } } }
.clientcontactfoot {
  position: absolute;
  bottom: -40%;
  transform: translateY(100%);
  right: 50%;
  transform: translateX(50%);
  @media only screen and (max-width: 1024px) {
    bottom: -25%; }
  @media only screen and (max-width: 768px) {
    bottom: -25%;
    right: 40%; }
  @media only screen and (max-width: 375px) {
    display: flex;
    flex-direction: column;
    bottom: 0;
    transform: translateY(0);
    right: 0;
    position: relative; }

  .number {
    display: flex;
    justify-content: space-around;
    padding-bottom: 20px;
    a {
      font-size: 18px;
      font-family: "OpenSans";
      font-weight: lighter;
      color: #dde7f2;
      text-decoration: none; }
    @media only screen and (max-width: 375px) {
      justify-content: space-between; } }
  .lifedel {
    p {
      font-size: 18px;
      font-family: "OpenSans";
      font-weight: lighter;
      color: #dde7f2; } } }
.clientimg {
  left: -25%;
  position: absolute;
  top: -15%;
  transform: translateY(-20%);
  transform: translateX(20%);
  @media only screen and (max-width: 1200px) {
    left: -17%; }
  @media only screen and (max-width: 1024px) {
    left: -20%; }
  @media only screen and (max-width: 768px) {
    top: 3%;
    img {
      width: 90%; } }
  @media only screen and (max-width: 375px) {
    display: none; } }
